* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box
}

body {
    margin: 0 auto;
    color: var(--colormain);
    font-family: arial, sans-serif;
    font-size: 14px;
    line-height: 1.4;
    background: var(--white);
}

.container {
    max-width: 1080px;margin: 0 auto;position: relative;
}

.width_common {
    width: 100%;
    float: left;
}
.flex{
    display: flex;flex-wrap: wrap;
}
.d-flex{
    display: flex;
}
.space-between{
    justify-content: space-between;
}
.inner_section {
    padding-left: 15px;
    padding-right: 15px;
}

.container_header {
    text-align: center;
    position: relative;
}

body.show_menu_full,
body.hidden {
    overflow: hidden;
    height: 100%;
}

a {
    color: var(--colormain);
    text-decoration: none;
    outline: none;
}

a:hover {
    text-decoration: none;
    color:#FF4B2B;
}
button{
    border: none;
    outline: none;
    cursor: pointer;
}
.icon-svg {
    width: 16px;
    height: 16px;
    fill: currentColor;
    vertical-align: middle;
    color: var(--gray3)
}
.ic {
    width: 16px;
    height: 16px;
    fill: #757575;
    display: inline-block;
    vertical-align: middle;
}
img {
    border: 0px;
    font-size: 0;
    line-height: 0;
    max-width: 100%;
    vertical-align: middle;
}

ul,
li {
    list-style-type: none;
}

input[type="text"], input[type="password"], input[type="email"], input[type="tel"], textarea, select {
    background: #fff;
    width: 100%;
    height: 40px;
    border: 1px solid #e5e5e5;
    font-size: 15px;
    padding: 0 12px;
    outline: none;
    font-family: arial;
    transition-duration: 200ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
}
.form_main .input_element {
    width: 100%;
    float: left;
    position: relative
}

.form_main .input_main {
    width: 100%;
    float: left;
    background: #FFFFFF;
    border: 1px solid #e5e5e5;
    border-radius: 3px;
    height: 40px;
    padding: 0 10px;
    color: #333;
    font-size: 15px;
    outline: none
}
.btn_vne {
