.thumb {
    display: block;
    overflow: hidden;
    height: 1px;
    position: relative;
    width: 100%;
    background: var(--colormain);

    img,video {
        object-position: center;
        object-fit: cover;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
    }
    
}
.embed-container img{
    object-position: center;
    object-fit: cover;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%!important
}
.thumb_5x3 {
    padding-bottom: 60%;
}
.thumb_5x4 {
    padding-bottom: 73.3%;
}

.thumb_16x9 {
    padding-bottom: 56.25%;

    .play_video {
        display: block;
    }
}

.thumb-video {
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    margin-bottom: 0;
    position: relative;
    overflow: hidden;
}
.play-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    svg{
        color: #fff;
        width: 60px;
        height: 60px;
    }
}
.thumb_5x5 {
    padding-bottom: 100%;
}
.thumb_2x3 {
    padding-bottom: 150%;
}
#to_top {
    width: 36px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    background: rgba(0, 0, 0, 0.1);
    position: fixed;
    right: 15px;
    bottom: 15px;
    border-radius: 50%;
    z-index: 99;
    display: none;
    svg{
        width: 23px;
        height: 23px;
        transform: rotate(-90deg);
    }
    i {
        color: #666;
    }
}

.sticky {
    position: sticky;
    top: 0;
}

.clearfix {
    clear: both;

    &:before,
    &:after {
        content: " ";
        display: table;
    }

    &:after {
        clear: both;
    }
}


.ic-live {
    display: inline-block;
    vertical-align: middle;
    color: #e78dac;
}

.ic-live:before {
    -webkit-border-radius: 50%;
    border-radius: 50%;
    display: block;
    position: relative;
    width: 8px;
    height: 8px;
    content: '';
    margin-right: 5px;
    vertical-align: initial;
    -webkit-animation: live-pulse 1s infinite;
    -moz-animation: live-pulse 1s infinite;
    -o-animation: live-pulse 1s infinite;
    animation: live-pulse 1s infinite;
    float: left;
}
.ic-live:after {
    content: 'Trực tiếp';
    font-family: arial;
    font-style: normal;
    color: #9f224e;
}
.ic-live.not-start:before {
    background: var(--gray3);
    box-shadow: 0 0 0 rgba(117,117,117,0.4);
    -webkit-animation: live-pulse-2 1s infinite;
    -moz-animation: live-pulse-2 1s infinite;
    -o-animation: live-pulse-2 1s infinite;
    animation: live-pulse-2 1s infinite;
}
.ic-live.not-start:after {
    content: 'Chưa bắt đầu';
    color: #222;
}
@-webkit-keyframes live-pulse {
    0% {
        background-color: #e78dac
    }

    50% {
        background-color: white
    }

    100% {
        background-color: #e78dac
    }
}

@-moz-keyframes live-pulse {
    0% {
        background-color: #e78dac
    }

    50% {
        background-color: white
    }

    100% {
        background-color: #e78dac
    }
}

@keyframes live-pulse {
    0% {
        background-color: #e78dac
    }

    50% {
        background-color: white
    }

    100% {
        background-color: #e78dac
    }
}